import type { LngLatBounds, Map as MapSDK, LngLat } from "@maptiler/sdk";
import type { TileLayer } from "../layers/TileLayer";
import { LRUCache } from "lru-cache";

/**
 * A number in [0, 255]
 */
export type ColorChannelValue = number; //0-255

/**
 * A number in [0, 1]
 */
export type AlphaChannelValue = number; // 0 - 1

/**
 * RgbChannel
 */
export type RgbChannel = "r" | "b" | "g";

/**
 * Channel(s) in a use by a layer
 */
export type UsedRgbChannel = RgbChannel | `${RgbChannel}${RgbChannel}`;

/**
 * Indices of RGBA image channels
 */
export enum ChannelIndices {
  r = 0,
  g = 1,
  b = 2,
  a = 3,
}

// Cache for GL textures to be used on picking
let textureCache: LRUCache<HTMLImageElement, WebGLTexture> | null = null;
let canvasForGlPicking: HTMLCanvasElement | null = null;
let glContextForPicking: WebGL2RenderingContext | null = null;

function getGlContextForPicking(): WebGL2RenderingContext {
  if (!canvasForGlPicking || !glContextForPicking) {
    canvasForGlPicking = document.createElement("canvas");
    glContextForPicking = canvasForGlPicking.getContext("webgl2");
    const maxNbTextures = glContextForPicking?.getParameter(glContextForPicking.MAX_TEXTURE_IMAGE_UNITS);

    textureCache = new LRUCache<HTMLImageElement, WebGLTexture>({
      max: maxNbTextures,

      // When a new texture added while the cache is full, we clear the least recently used texture from GPU memory
      dispose: (value, _key) => {
        const gl = getGlContextForPicking();
        gl.deleteTexture(value);
      },
    });
  }

  return <WebGL2RenderingContext>glContextForPicking;
}

/**
 * Defines a color type made of 3 ColorChannelValue OR 3 ColorChannelValue and one AlphaChannelValue
 */
export type RgbaColor =
  | [ColorChannelValue, ColorChannelValue, ColorChannelValue]
  | [ColorChannelValue, ColorChannelValue, ColorChannelValue, AlphaChannelValue];

/**
 * Turns a RgbaColor into a string GLSL color constructor.
 * @param color
 * @returns a GLSL vec4 color constructor
 */
export function floatifyColor(color: RgbaColor): string {
  return (
    `vec4(${(color[0] / 255).toFixed(3)},` +
    `${(color[1] / 255).toFixed(3)},` +
    `${(color[2] / 255).toFixed(3)},` +
    `${((color[3] ?? 255) / 255).toFixed(3)})`
  );
}

/**
 * Turns a [r, g, b] or [r, g, b, a] color defined on the [0, 255] range
 * into a [r, g, b, a] color in the [0, 1] range
 * @param color
 * @returns
 */
export function makeUnitColor(color: RgbaColor): RgbaColor {
  return [color[0] / 255, color[1] / 255, color[2] / 255, (color[3] ?? 255) / 255];
}

/**
 * Converts a latitude in degree into a unit value.
 * See the function here: https://www.desmos.com/calculator/ptaooqjt8c
 * @param lat
 * @returns
 */
export function latToUnit(lat: number) {
  return ((180 / Math.PI) * Math.log(Math.tan(Math.PI / 4 + (lat * Math.PI) / 360))) / 360;
}

/**
 * Converts WGS84 coordinates to [-0.5, 0.5] range.
 * @param lng
 * @param lat
 * @internal
 */
export function wgs84ToUnit(lng: number, lat: number) {
  return [lng / 360, latToUnit(lat)] as const;
}

/**
 * Converts unit range [-0.5, 0.5] to WGS84 coordinates.
 * @param unit
 * @returns
 */
export function unitToLat(unit: number) {
  const pi = Math.PI;
  const atan = Math.atan;
  const exp = Math.exp;

  const thing1 = exp(unit * 2 * pi);
  const thing2 = atan(thing1) - pi / 4;
  return (thing2 * 360) / pi;
}

/**
 * With a mercator image (origin: top left), return the latitude from the y in pixel coordinates
 * @param y
 * @param imageSize
 * @returns
 */
export function pixelToLat(y: number, imageSize: number): number {
  const unit = -0.5 * ((y / imageSize) * 2 - 1);
  return unitToLat(unit);
}

/**
 * Radius of the Earth in meter
 */
export const EARTH_RADIUS_M = 6378137;

/**
 * Perimeter of the Earth alongside the equator in meter
 */
export const EARTH_PERIMETER_M = 2 * Math.PI * EARTH_RADIUS_M;

/**
 * Converts wgs84 (longitude and latitude) into Mercator coordinates (in meters)
 * @param lng
 * @param lat
 * @returns
 */
export function wgs84ToMerc(lng: number, lat: number) {
  return wgs84ToUnit(lng, lat).map((a) => a * EARTH_PERIMETER_M);
}

