/**
 * A Pixel as in HTMLImageElement, with the form `{r, g, b, a}`
 */
export interface Pixel extends Record<RgbChannel, ColorChannelValue> {
  a: AlphaChannelValue;
}

/**
 * Get the pixel value of an image at a given position
 * @param image
 * @param x
 * @param y
 * @returns The pixel as `{r, g, b, a}` in a [0, 1] interval (and not in [0, 255])
 */
export function sampleImage(image: HTMLImageElement, x: number, y: number): Pixel {
  // For future maintainer:
  // picking a pixel value using a WebGL2 context instead of a simple canvas
  // may seem overkill, yet, canvas are applying alpha premultiplication, meaning they mess up RGB
  // values if the alpha value is not 255. This is usually not a big deal when the image contains colors
  // but this could lead to significant issues if the image contains non-color data, such as terrain RGB
  // or radar data making use of the alpha channel to use as a category.
  // More info about this issue here:
  // https://stackoverflow.com/questions/23497925/how-can-i-stop-the-alpha-premultiplication-with-canvas-imagedata
  const xImage = Math.floor(x * image.width);
  const yImage = Math.floor(y * image.height);
  const gl = getGlContextForPicking();

  if (gl == null) {
    throw new Error("Failed to obtain WebGL2 context");
  }

  if (!textureCache) {
    throw new Error("Could not initialize texture cache");
  }

  gl.activeTexture(gl.TEXTURE0);
  let texture: WebGLTexture;

  if (textureCache.has(image)) {
    texture = textureCache.get(image) as WebGLTexture;
  } else {
    texture = gl.createTexture() as WebGLTexture;
    gl.bindTexture(gl.TEXTURE_2D, texture);
    gl.texImage2D(gl.TEXTURE_2D, 0, gl.RGBA, gl.RGBA, gl.UNSIGNED_BYTE, image);
    textureCache.set(image, texture);
  }

  const framebuffer = gl.createFramebuffer();
  gl.bindFramebuffer(gl.FRAMEBUFFER, framebuffer);
  gl.framebufferTexture2D(gl.FRAMEBUFFER, gl.COLOR_ATTACHMENT0, gl.TEXTURE_2D, texture, 0);

  gl.drawBuffers([gl.COLOR_ATTACHMENT0]);

  const data = new Uint8Array(4);
  gl.readPixels(xImage, yImage, 1, 1, gl.RGBA, gl.UNSIGNED_BYTE, data);
  gl.deleteFramebuffer(framebuffer);

  return {
    r: data[0] / 255,
    g: data[1] / 255,
    b: data[2] / 255,
    a: data[3] / 255,
  };
}

export function addLayerWhenReady(map: MapSDK, layer: TileLayer, beforeId = "") {
  if (map.isStyleLoaded()) {
    map.addLayer(layer, beforeId);
  } else {
    map.once("load", () => {
      map.addLayer(layer, beforeId);
    });
  }
}

/**
 * Get the cardinal direction based on an angle with
 * - `0`: true north
 * - `90`: east
 * - `180`: south
 * - `270`: west
 * @param angle
 * @returns
 */
export function getCardinalDirection(angle: number) {
  // Define array of direction names
  const directions = [
    "N",
    "NNE",
    "NE",
    "ENE",
    "E",
    "ESE",
    "SE",
    "SSE",
    "S",
    "SSW",
    "SW",
    "WSW",
    "W",
    "WNW",
    "NW",
    "NNW",
  ];

  // Calculate index of closest direction based on angle
  const index = Math.round(((angle + 360) % 360) / 22.5) % 16;

  // Return corresponding direction name
  return directions[index];
}

export function wgs84ToMaplibreMerc(lng: number, lat: number) {
  const merc = wgs84ToUnit(lng, lat);
  return [merc[0] + 0.5, merc[1] * -1 + 0.5];
}

export function wgs84ToTileIndex(lngLat: LngLat, z: number) {
  const mercCoord = wgs84ToMaplibreMerc(lngLat.lng, lngLat.lat);
  const nbTile = 2 ** z;
  return {
    z: z,
    x: mercCoord[0] * nbTile,
    y: mercCoord[1] * nbTile,
  };
}

function getTileCoverage(wgs84Bound: LngLatBounds, z: number): string[] {
  const ceiledZ = ~~z;
  const topLeft = wgs84ToTileIndex(wgs84Bound.getNorthWest(), ceiledZ);
  const bottomRight = wgs84ToTileIndex(wgs84Bound.getSouthEast(), ceiledZ);
  const tiles = [];

  for (let x = Math.floor(topLeft.x); x <= bottomRight.x; x += 1) {
    for (let y = Math.floor(topLeft.y); y <= Math.floor(bottomRight.y); y += 1) {
      tiles.push(`${z}/${x}/${y}`);
    }
  }

  return tiles;
}

export { getTileCoverage };
